// app/tecnico/trabajos/[id]/page.jsx
"use client";
import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabaseClient";
import { useRouter, useParams } from "next/navigation";

export default function TrabajoEnProgresoPage() {
  const [tarea, setTarea] = useState(null);
  const [orden, setOrden] = useState(null);
  const [maquina, setMaquina] = useState(null);
  const [cliente, setCliente] = useState(null);
  const [evidencias, setEvidencias] = useState([]);
  const [nuevaEvidencia, setNuevaEvidencia] = useState("");
  const [descripcionEvidencia, setDescripcionEvidencia] = useState("");
  const [observaciones, setObservaciones] = useState("");
  const [loading, setLoading] = useState(true);
  const router = useRouter();
  const params = useParams();

  useEffect(() => {
    cargarDatos();
    
    // Auto-guardar observaciones cada 30 segundos
    const interval = setInterval(() => {
      if (observaciones) {
        guardarObservaciones(true);
      }
    }, 30000);

    return () => clearInterval(interval);
  }, []);

  async function cargarDatos() {
    try {
      // Cargar tarea
      const { data: tareaData } = await supabase
        .from("tareas")
        .select("*")
        .eq("id", params.id)
        .single();

      if (!tareaData) {
        alert("Tarea no encontrada");
        router.push("/tecnico");
        return;
      }

      setTarea(tareaData);
      setEvidencias(tareaData.evidencias || []);
      setObservaciones(tareaData.observaciones || "");

      // Cargar orden
      const { data: ordenData } = await supabase
        .from("ordenes")
        .select("*")
        .eq("id", tareaData.orden_id)
        .single();

      setOrden(ordenData);

      // Cargar m√°quina
      const { data: maquinaData } = await supabase
        .from("maquinas")
        .select("*")
        .eq("id", ordenData.maquina_id)
        .single();

      setMaquina(maquinaData);

      // Cargar cliente
      if (maquinaData?.cliente_id) {
        const { data: clienteData } = await supabase
          .from("clientes")
          .select("*")
          .eq("id", maquinaData.cliente_id)
          .single();
        setCliente(clienteData);
      }
    } catch (error) {
      console.error("Error:", error);
    } finally {
      setLoading(false);
    }
  }

  async function agregarEvidencia() {
    if (!nuevaEvidencia.trim()) {
      alert("Por favor ingresa la URL de la foto");
      return;
    }

    const nuevasEvidencias = [
      ...evidencias,
      {
        url: nuevaEvidencia,
        descripcion: descripcionEvidencia,
        fecha: new Date().toISOString()
      }
    ];

    setEvidencias(nuevasEvidencias);
    setNuevaEvidencia("");
    setDescripcionEvidencia("");

    // Guardar en la base de datos
    await supabase
      .from("tareas")
      .update({ evidencias: nuevasEvidencias })
      .eq("id", params.id);

    alert("Evidencia agregada correctamente");
  }

  async function eliminarEvidencia(index) {
    if (!confirm("¬øEliminar esta evidencia?")) return;

    const nuevasEvidencias = evidencias.filter((_, i) => i !== index);
    setEvidencias(nuevasEvidencias);

    await supabase
      .from("tareas")
      .update({ evidencias: nuevasEvidencias })
      .eq("id", params.id);
  }

  async function guardarObservaciones(silencioso = false) {
    try {
      await supabase
        .from("tareas")
        .update({ observaciones })
        .eq("id", params.id);

      if (!silencioso) {
        alert("Observaciones guardadas");
      }
    } catch (error) {
      console.error("Error al guardar observaciones:", error);
    }
  }

  async function finalizarTrabajo() {
    if (evidencias.length === 0) {
      if (!confirm("No has agregado evidencias fotogr√°ficas. ¬øContinuar sin evidencias?")) {
        return;
      }
    }

    if (!observaciones.trim()) {
      if (!confirm("No has agregado observaciones. ¬øContinuar sin observaciones?")) {
        return;
      }
    }

    if (!confirm("¬øEst√°s seguro de finalizar este trabajo?")) return;

    try {
      const ahora = new Date().toISOString();
      const inicio = new Date(tarea.inicio);
      const duracion = Math.round((new Date(ahora) - inicio) / 60000); // minutos

      // Actualizar tarea
      await supabase
        .from("tareas")
        .update({
          estado: "completada",
          fin: ahora,
          completada_at: ahora,
          observaciones: observaciones,
          evidencias: evidencias
        })
        .eq("id", params.id);

      // Actualizar orden
      await supabase
        .from("ordenes")
        .update({ estado: "completada" })
        .eq("id", tarea.orden_id);

      // Guardar en historial
      await supabase
        .from("historial_trabajos")
        .insert({
          orden_id: orden.id,
          tarea_id: tarea.id,
          tecnico_id: tarea.tecnico_id,
          maquina_id: orden.maquina_id,
          descripcion: orden.descripcion,
          tipo: orden.tipo,
          estado: "completada",
          evidencias: evidencias,
          observaciones: observaciones,
          inicio: tarea.inicio,
          fin: ahora,
          duracion_minutos: duracion
        });

      alert("‚úÖ Trabajo finalizado correctamente");
      router.push("/tecnico");
    } catch (error) {
      console.error("Error al finalizar:", error);
      alert("Error al finalizar trabajo: " + error.message);
    }
  }

  function calcularTiempoTranscurrido() {
    if (!tarea?.inicio) return "0 min";
    
    const inicio = new Date(tarea.inicio);
    const ahora = new Date();
    const minutos = Math.round((ahora - inicio) / 60000);
    
    if (minutos < 60) return `${minutos} min`;
    
    const horas = Math.floor(minutos / 60);
    const mins = minutos % 60;
    return `${horas}h ${mins}min`;
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600">Cargando informaci√≥n...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-5xl mx-auto">
        {/* Header */}
        <div className="bg-gradient-to-r from-blue-600 to-blue-700 text-white rounded-lg shadow-lg p-6 mb-6">
          <div className="flex justify-between items-start">
            <div>
              <h1 className="text-2xl font-bold mb-2">üîß Trabajo en Progreso</h1>
              <p className="text-blue-100">M√°quina: {maquina?.codigo}</p>
              <p className="text-blue-100 text-sm">Cliente: {cliente?.nombre}</p>
            </div>
            <div className="text-right">
              <div className="bg-white/20 px-4 py-2 rounded-lg backdrop-blur">
                <p className="text-sm text-blue-100">Tiempo transcurrido</p>
                <p className="text-2xl font-bold">{calcularTiempoTranscurrido()}</p>
              </div>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Columna Principal */}
          <div className="lg:col-span-2 space-y-6">
            {/* Informaci√≥n del Trabajo */}
            <div className="bg-white rounded-lg shadow p-6">
              <h2 className="font-bold text-lg mb-4">üìã Descripci√≥n del Trabajo</h2>
              <div className="bg-yellow-50 border-l-4 border-yellow-400 p-4 rounded">
                <p className="font-medium text-yellow-800">Problema Reportado:</p>
                <p className="text-gray-700 mt-1">{orden?.descripcion || "Sin descripci√≥n"}</p>
              </div>
              
              <div className="grid grid-cols-2 gap-4 mt-4">
                <div className="bg-gray-50 p-3 rounded">
                  <p className="text-sm text-gray-600">Tipo</p>
                  <p className="font-medium capitalize">{orden?.tipo}</p>
                </div>
                <div className="bg-gray-50 p-3 rounded">
                  <p className="text-sm text-gray-600">Prioridad</p>
                  <span className={`inline-block px-3 py-1 rounded-full text-sm font-medium ${
                    orden?.prioridad === 'urgente' ? 'bg-red-100 text-red-800' :
                    orden?.prioridad === 'alta' ? 'bg-orange-100 text-orange-800' :
                    orden?.prioridad === 'media' ? 'bg-yellow-100 text-yellow-800' :
                    'bg-blue-100 text-blue-800'
                  }`}>
                    {orden?.prioridad}
                  </span>
                </div>
              </div>
            </div>

            {/* Evidencias Fotogr√°ficas */}
            <div className="bg-white rounded-lg shadow p-6">
              <h2 className="font-bold text-lg mb-4">üì∏ Evidencias Fotogr√°ficas</h2>
              
              {/* Agregar nueva evidencia */}
              <div className="mb-6 p-4 bg-blue-50 rounded-lg border border-blue-200">
                <p className="font-medium mb-3 text-blue-900">Agregar nueva foto</p>
                <input
                  type="url"
                  placeholder="URL de la foto (https://...)"
                  value={nuevaEvidencia}
                  onChange={(e) => setNuevaEvidencia(e.target.value)}
                  className="w-full p-2 border rounded mb-2 focus:ring-2 focus:ring-blue-500 outline-none"
                />
                <input
                  type="text"
                  placeholder="Descripci√≥n de la foto (opcional)"
                  value={descripcionEvidencia}
                  onChange={(e) => setDescripcionEvidencia(e.target.value)}
                  className="w-full p-2 border rounded mb-3 focus:ring-2 focus:ring-blue-500 outline-none"
                />
                <button
                  onClick={agregarEvidencia}
                  className="w-full py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition font-medium"
                >
                  + Agregar Foto
                </button>
              </div>

              {/* Lista de evidencias */}
              {evidencias.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  <p className="text-4xl mb-2">üì∑</p>
                  <p>No hay evidencias fotogr√°ficas a√∫n</p>
                </div>
              ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {evidencias.map((ev, index) => (
                    <div key={index} className="border rounded-lg overflow-hidden hover:shadow-lg transition">
                      <img
                        src={ev.url}
                        alt={`Evidencia ${index + 1}`}
                        className="w-full h-48 object-cover"
                        onError={(e) => {
                          e.target.src = "https://via.placeholder.com/400x300?text=Error+al+cargar+imagen";
                        }}
                      />
                      <div className="p-3">
                        {ev.descripcion && (
                          <p className="text-sm text-gray-700 mb-2">{ev.descripcion}</p>
                        )}
                        <div className="flex justify-between items-center">
                          <p className="text-xs text-gray-500">
                            {new Date(ev.fecha).toLocaleString("es-ES")}
                          </p>
                          <button
                            onClick={() => eliminarEvidencia(index)}
                            className="text-red-600 hover:text-red-800 text-sm font-medium"
                          >
                            Eliminar
                          </button>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>

            {/* Observaciones */}
            <div className="bg-white rounded-lg shadow p-6">
              <h2 className="font-bold text-lg mb-4">üìù Observaciones del T√©cnico</h2>
              <textarea
                value={observaciones}
                onChange={(e) => setObservaciones(e.target.value)}
                placeholder="Describe el trabajo realizado, repuestos utilizados, recomendaciones, etc..."
                className="w-full p-3 border rounded focus:ring-2 focus:ring-blue-500 outline-none"
                rows="6"
              />
              <button
                onClick={() => guardarObservaciones(false)}
                className="mt-3 px-6 py-2 bg-gray-600 text-white rounded hover:bg-gray-700 transition"
              >
                üíæ Guardar Observaciones
              </button>
              <p className="text-xs text-gray-500 mt-2">
                * Las observaciones se guardan autom√°ticamente cada 30 segundos
              </p>
            </div>
          </div>

          {/* Columna Lateral */}
          <div className="space-y-6">
            {/* Perfil de la M√°quina */}
            <div className="bg-white rounded-lg shadow p-6 sticky top-6">
              <h3 className="font-bold text-lg mb-4">üöú Perfil de la M√°quina</h3>
              
              {maquina?.foto_url && (
                <img
                  src={maquina.foto_url}
                  alt={maquina.codigo}
                  className="w-full h-40 object-cover rounded-lg mb-4"
                />
              )}

              <div className="space-y-3 text-sm">
                <div className="bg-blue-50 p-3 rounded">
                  <p className="text-xs text-gray-600">C√≥digo</p>
                  <p className="font-bold text-lg">{maquina?.codigo}</p>
                </div>
                
                <div className="grid grid-cols-2 gap-2">
                  <div className="bg-gray-50 p-2 rounded">
                    <p className="text-xs text-gray-600">Marca</p>
                    <p className="font-medium">{maquina?.marca || "N/A"}</p>
                  </div>
                  <div className="bg-gray-50 p-2 rounded">
                    <p className="text-xs text-gray-600">Modelo</p>
                    <p className="font-medium">{maquina?.modelo || "N/A"}</p>
                  </div>
                </div>

                <div className="bg-gray-50 p-2 rounded">
                  <p className="text-xs text-gray-600">Serie</p>
                  <p className="font-medium">{maquina?.serie || "N/A"}</p>
                </div>

                <div className="bg-green-50 p-3 rounded border border-green-200">
                  <p className="text-xs text-gray-600">Hor√≥metro</p>
                  <p className="font-bold text-xl text-green-700">{maquina?.horometro} hrs</p>
                </div>

                <div>
                  <p className="text-xs text-gray-600 mb-1">Estado</p>
                  <span className={`inline-block px-3 py-1 rounded-full text-xs font-medium ${
                    maquina?.estado === 'operativa' ? 'bg-green-100 text-green-800' :
                    maquina?.estado === 'mantenimiento' ? 'bg-yellow-100 text-yellow-800' :
                    'bg-red-100 text-red-800'
                  }`}>
                    {maquina?.estado}
                  </span>
                </div>

                {cliente && (
                  <>
                    <hr className="my-3" />
                    <div>
                      <p className="text-xs text-gray-600 font-medium mb-2">Informaci√≥n del Cliente</p>
                      <p className="font-medium">{cliente.nombre}</p>
                      {cliente.telefono && (
                        <p className="text-gray-600 mt-1">üìû {cliente.telefono}</p>
                      )}
                      {cliente.direccion && (
                        <p className="text-gray-600 text-xs mt-1">üìç {cliente.direccion}</p>
                      )}
                    </div>
                  </>
                )}
              </div>
            </div>

            {/* Bot√≥n Finalizar */}
            <button
              onClick={finalizarTrabajo}
              className="w-full py-4 bg-green-600 text-white rounded-lg hover:bg-green-700 transition font-bold text-lg shadow-lg hover:shadow-xl"
            >
              ‚úÖ Finalizar Trabajo
            </button>

            <button
              onClick={() => router.push("/tecnico")}
              className="w-full py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition"
            >
              ‚Üê Volver
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}